import QSTabs from '../../components/QS-tabs/QS-tabs.vue';
import uniGrid from '../../components/uni-grid/uni-grid.vue';
import uniGridItem from '../../components/uni-grid-item/uni-grid-item.vue';


export default {
	components: {
		QSTabs,
		uniGrid,
		uniGridItem
	},
	
	onLoad(options) {
		// console.log(options);
		this.uid = options.uid;
	},
	onShow() {
		this.getUserInfo();
		this.getWorksByUserId();
		this.getImagesByUserId(this.uid);
		
		
	},
	data() {
		return {
			br:'\n',
			uid:'',
			userinfo:{},
			newestApt:[],
			tabs: ['TA的作品','TA的相册','TA的约拍'],
			current: 0,
			tabsHeight: 0,
			dx: 0,
			works:{},
			images:[]
		}
	},
	methods: {
		change(index) {
			this.current = index;
		},
		swiperChange({detail: { current }}) {
			this.current = current;
		},
		transition({detail: { dx }}) {
			this.$refs.tabs.setDx(dx);
		},
		animationfinish({detail: { current }}) {
			this.$refs.tabs.setFinishCurrent(current);
		},
		getUserInfo:function(){
			uni.request({
				url:this.createApiUrl('/user/get/user'),
				data:{
					uid:this.uid
				},
				success:res => {
					// console.log(res);
					this.userinfo = res.data.data;
				}
			})
		},
		getWorksByUserId:function(){
			uni.request({
				url:this.createApiUrl('/works/get/uid'),
				data:{
					uid:this.uid
				},
				success: (res) => {
					this.works = res.data.data;
					console.log(this.works);
				}
			})
		},
		
		//获取相册图片
		getImagesByUserId:function(uid){
			uni.request({
				url:this.createApiUrl('user/images'),
				data:{
					uid:uid
				},
				success: (res) => {
					// console.log(res);
					this.images = res.data.data;
					// console.log(this.images);
					
				}
			})
		},
	}
}
